import VueRouter from 'vue-router'

//引入组件
import Index from '../pages'
import Home from '../pages/home/home'
import Login from '../pages/login/login'
import Register from '../pages/register/register'
import Center from '../pages/center/center'
import Storeup from '../pages/storeup/list'
import AddrList from '../pages/shop-address/list'
import AddrAdd from '../pages/shop-address/addOrUpdate'
import Order from '../pages/shop-order/list'
import OrderConfirm from '../pages/shop-order/confirm'
import Cart from '../pages/shop-cart/list'
import News from '../pages/news/news-list'
import NewsDetail from '../pages/news/news-detail'
import yonghuList from '../pages/yonghu/list'
import yonghuDetail from '../pages/yonghu/detail'
import yonghuAdd from '../pages/yonghu/add'
import zhongzhihuList from '../pages/zhongzhihu/list'
import zhongzhihuDetail from '../pages/zhongzhihu/detail'
import zhongzhihuAdd from '../pages/zhongzhihu/add'
import nongchanpinleixingList from '../pages/nongchanpinleixing/list'
import nongchanpinleixingDetail from '../pages/nongchanpinleixing/detail'
import nongchanpinleixingAdd from '../pages/nongchanpinleixing/add'
import nongzileixingList from '../pages/nongzileixing/list'
import nongzileixingDetail from '../pages/nongzileixing/detail'
import nongzileixingAdd from '../pages/nongzileixing/add'
import nongchanpinList from '../pages/nongchanpin/list'
import nongchanpinDetail from '../pages/nongchanpin/detail'
import nongchanpinAdd from '../pages/nongchanpin/add'
import nongzichanpinList from '../pages/nongzichanpin/list'
import nongzichanpinDetail from '../pages/nongzichanpin/detail'
import nongzichanpinAdd from '../pages/nongzichanpin/add'
import nongzijishuList from '../pages/nongzijishu/list'
import nongzijishuDetail from '../pages/nongzijishu/detail'
import nongzijishuAdd from '../pages/nongzijishu/add'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

//配置路由
export default new VueRouter({
	routes:[
		{
      path: '/',
      redirect: '/index/home'
    },
		{
			path: '/index',
			component: Index,
			children:[
				{
					path: 'home',
					component: Home
				},
				{
					path: 'center',
					component: Center,
				},
				{
					path: 'storeup',
					component: Storeup
				},
                {
                    path: 'shop-address/list',
                    component: AddrList
                },
                {
                    path: 'shop-address/addOrUpdate',
                    component: AddrAdd
                },
				{
					path: 'shop-order/order',
					component: Order
				},
				{
					path: 'cart',
					component: Cart
				},
				{
					path: 'shop-order/orderConfirm',
					component: OrderConfirm
				},
				{
					path: 'news',
					component: News
				},
				{
					path: 'newsDetail',
					component: NewsDetail
				},
				{
					path: 'yonghu',
					component: yonghuList
				},
				{
					path: 'yonghuDetail',
					component: yonghuDetail
				},
				{
					path: 'yonghuAdd',
					component: yonghuAdd
				},
				{
					path: 'zhongzhihu',
					component: zhongzhihuList
				},
				{
					path: 'zhongzhihuDetail',
					component: zhongzhihuDetail
				},
				{
					path: 'zhongzhihuAdd',
					component: zhongzhihuAdd
				},
				{
					path: 'nongchanpinleixing',
					component: nongchanpinleixingList
				},
				{
					path: 'nongchanpinleixingDetail',
					component: nongchanpinleixingDetail
				},
				{
					path: 'nongchanpinleixingAdd',
					component: nongchanpinleixingAdd
				},
				{
					path: 'nongzileixing',
					component: nongzileixingList
				},
				{
					path: 'nongzileixingDetail',
					component: nongzileixingDetail
				},
				{
					path: 'nongzileixingAdd',
					component: nongzileixingAdd
				},
				{
					path: 'nongchanpin',
					component: nongchanpinList
				},
				{
					path: 'nongchanpinDetail',
					component: nongchanpinDetail
				},
				{
					path: 'nongchanpinAdd',
					component: nongchanpinAdd
				},
				{
					path: 'nongzichanpin',
					component: nongzichanpinList
				},
				{
					path: 'nongzichanpinDetail',
					component: nongzichanpinDetail
				},
				{
					path: 'nongzichanpinAdd',
					component: nongzichanpinAdd
				},
				{
					path: 'nongzijishu',
					component: nongzijishuList
				},
				{
					path: 'nongzijishuDetail',
					component: nongzijishuDetail
				},
				{
					path: 'nongzijishuAdd',
					component: nongzijishuAdd
				},
			]
		},
		{
			path: '/login',
			component: Login
		},
		{
			path: '/register',
			component: Register
		},
	]
})
